* {
  margin: 0;
  padding: 0;
}

.navbar {
  width: 100%;
  height: 100px;
  background-color: #f44336;
}

.nav_box {
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.my_shop {
  font-size: 2rem;
  font-weight: bold;
  color: white;
  cursor: pointer;
}
.cart {
  cursor: pointer;
}
.cart span:nth-child(1) {
  font-size: 2rem;
  color: white;
}
.cart span:nth-child(2) {
  padding: 0 3px;
  background-color: red;
  color: white;
  font-weight: bolx;
  border-radius: 5px;
  position: relative;
  top: -15px;
}
